import React, { useState, setState, useEffect } from 'react';
import styled from 'styled-components'

const Preference = styled.div`
    box-shadow: -5px 0px 10px rgba(0,0,0,0.5);
    width: 30%;
    background:#001A34;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 1;
`;
const Upper = styled.div`
padding: 0 2rem;
    p{
        font-size: 45px;
        text-align: left;
        @media only screen and (max-width:800px){
        font-size: 22px;
        }
    }
    div{
        @media only screen and (max-width:800px){
        width: 50%;
        float: left;
        }
    }
`;
const Lower = styled.div`
    label{
        font-size: 45px;
        text-align: left;
        @media only screen and (max-width:800px){
            font-size: 22px;
        }
    }
    select{
        font-size: 30px;
        border-radius: 30px;
        margin-left: 1rem;
        text-align: center;
        @media only screen and (max-width:800px){
            font-size: 22px;
        }
    }
`;

const Prefs = (props) => {

    const [level, setLevel] = useState("easy");
    

    function changeLevel(e){
        setLevel(e.target.value);
        return {level}
    }

  return (<>
    <Preference>
    <Upper>
        <h1>ReactJS Memory</h1>
        <div>
            <p>Turns: {props.counter}</p>
        </div>
        <div>
            <p>Score:</p>
        </div>
    </Upper>
    <Lower>
        <label for="level">Level:</label>
        <select id="level" onChange={(e) => props.clickFunction(e)} levelValue={props.levelValue}>
            <option value="1" onChange={() => setLevel("1")}>Easy</option>
            <option value="2" onChange={() => setLevel("2")}>Middle</option>
            <option value="3" onChange={() => setLevel("3")}>Hard</option>
        </select>
        <button className="restart" onClick={(e) => props.startFunction(e)}>Start</button>
    </Lower>
    </Preference>
    </>
  );
}

export default Prefs